}
.contact__information {
  display: block;
  font-size: 16px;
  color: #59a8dd;
  cursor: pointer;
  margin-left: 0;
}
.contact__information--our-business {
  display: inline-block;
  text-decoration: underline;
}
.contact a[href]:hover,
.contact a[href]:focus {
  color: #1a5b87;
