.social-icon {
  position: relative;
  display: flex;
  list-style-type: none;
  padding: 0;
}
@media (max-width: 780px) and (min-width: 481px) {
  .social-icon {
    justify-content: center;
  }
}
@media (max-width: 480px) {
  .social-icon {
    justify-content: center;
  }
}
.social-icon__link {
  display: flex;
  width: 36px;
  height: 36px;
